* {
    box-sizing: border-box;
}

body {
    background-color: #fafafa;
    margin: 0;
    font-family: "Roboto", sans-serif;
}

a {
    text-decoration: none;
    &:hover {
        text-decoration: none;
        color: $blue;
    }
}

.container {
    max-width: 1110px;
    margin-left: auto;
    margin-right: auto;
}

.hidden {
    display: none;
}
